
import { defineComponent } from "vue";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { APM20100111_BODY, APM20100111_RES, ELEMENT, PORTAL_AUTHORIZATION_CODE_LIST } from "@/shared/type/APM20100111.type";
import Pagination from '@/shared/components/Pagination/Pagination.vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
  name: "apm201001sc00",
  components: {
    Pagination,
    BreadCrumb,
    SelectOption
  },

  data() {
    const dataTable = [] as PORTAL_AUTHORIZATION_CODE_LIST[];
    return {
      dataTable,
      searchField: {
        authorizationBizTypeCode1: "",
        businessCommonStatusCode: "",
      },
      totalItems: 0,
      currentPage: 1,
      pageSize: PAGE_SIZE,
      startingIndex: 1,
      resetPage: false,
    };
  },

  mounted() {
    this.inquiryAuthorizationCodeList();
  },

  computed: {
    headers() {
      return [
        { columnName: "No." },
        { columnName: "View" },
        { columnName: "Authorization Code 1" },
        { columnName: "Authorization Code 2" },
        { columnName: "Status" },
      ]
    },
  },

  methods: {

    async inquiryAuthorizationCodeList(mode?: string) {
      const _oBody: APM20100111_BODY = {
        authorizationBizTypeCode1: this.searchField.authorizationBizTypeCode1,
        businessCommonStatusCode: this.searchField.businessCommonStatusCode,
        pageSize: this.pageSize,
        pageNumber: mode == 'search' ? 0 : this.currentPage
      };

      if (mode === 'search') {
        this.currentPage = 1
        this.resetPage = true;
      }

      const res = await this.$request.getRequest("APM20100111", _oBody, false) as APM20100111_RES;

      if (res.header.result) {

        this.dataTable = JSON.parse(JSON.stringify(this.convertCodeToValue(res.body.list)))

        this.dataTable.map(
          (item: PORTAL_AUTHORIZATION_CODE_LIST) => {
            let temp =
              this.$store.state.CommonCodeModule.commonCode.BusinessCategoryCode.find(
                (element: ELEMENT) => element.key === item.authorizationBizTypeCode1
              );
            if (temp.key) {
              item.authorizationBizTypeCode1Name = temp.value;
            }

            return item;
          }
        );

        this.totalItems = res.body.totalCount;
      }
    },

    convertCodeToValue(data: PORTAL_AUTHORIZATION_CODE_LIST[]): PORTAL_AUTHORIZATION_CODE_LIST[] {
      return data.map((item: PORTAL_AUTHORIZATION_CODE_LIST) => {
        return {
          authorizationBizTypeCode1: item.authorizationBizTypeCode1,
          authorizationBizTypeCode2: item.authorizationBizTypeCode2,
          managerApprovalDescription: item.managerApprovalDescription,
          authorizationBizTypeCode1Name: item.authorizationBizTypeCode1Name,
          authorizationAuthorityTypeCode: item.authorizationAuthorityTypeCode,
          businessCommonStatusCode: `${item.businessCommonStatusCode} : ${this.$codeConverter.convertValue('BusinessCommonStatusCode', item.businessCommonStatusCode)}`
        }
      })
    },

    refreshSearch() {
      this.searchField.authorizationBizTypeCode1 = "";
      this.searchField.businessCommonStatusCode = "";
      this.resetPage = true;
      this.currentPage = 1;
      this.inquiryAuthorizationCodeList();
    },

    handlePagination(pageNumber: number) {
      this.currentPage = pageNumber;
      this.resetPage = false;
      this.inquiryAuthorizationCodeList()
    },

    handleStartingIndex(startingIndex: number) {
      this.startingIndex = startingIndex
    },

    onClickRegister() {
      this.$router.push({
        path: "/APM201001SC01",
        query: {
          type: "register",
        },
      });
    },

    handleClickAction(actionType: string, index: number, rowData: PORTAL_AUTHORIZATION_CODE_LIST) {
      if (actionType === 'detail') {
        this.$router.push({
          path: 'APM201001SC01',
          query: {
            index: index,
            type: "edit",
            authorizationBizTypeCode1: rowData.authorizationBizTypeCode1,
            authorizationBizTypeCode2: rowData.authorizationBizTypeCode2,
            businessCommonStatusCode: rowData.businessCommonStatusCode.split(' : ')[0],
            managerApprovalDescription: rowData.managerApprovalDescription,
            authorizationBizTypeCode1Name: rowData.authorizationBizTypeCode1Name,
            authorizationAuthorityTypeCode: rowData.authorizationAuthorityTypeCode,
          }
        });
      }
    },
  },
});
